.feature-image-container {
    width: calc(100% + 40px);
    margin: 50px;
    height: 80%;
    padding: 0px;
    background-color: transparent;
    border-radius: 10px;
    display: flex;
    overflow: hidden;
    transition: 0.25s ease-in;
}

.feature-image-container-hover {
    width: calc(90%);
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 20px;
    margin-bottom: 20px;
    height: 60%;
    /* padding: 20px 0; */
    background-color: transparent;
    border-radius: 5px;
    display: flex;
    overflow: hidden;
    transition: 0.25s ease-in;
}

.feature-0 video, .feature-1 video {
    transform: scale(1.4);
}

 
.feature-image-container-hover img , .feature-image-container video {
    width: 100%;
    object-fit: fill;
    transition: 0.25s ease-in-out !important;
    border-radius: 10px;
}

.feature-image-container img , .feature-image-container video {
    width: 100%;
    object-fit: fill;
    transition: 0.25s ease-in-out !important;
    border-radius: 10px;
}

.feature-info {
    height: 20%;
    overflow: hidden;
    padding: 0 10%;
    width: 100%;
    transition: 0.25s ease-in-out;
}

.feature-info-hover {
    height: 60%;
    overflow: hidden;
    padding: 0 10%;
    width: 100%;
    transition: 0.25s ease-in-out;
}

.itsa-def {
