}

.double-card {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.double-card-upper {
    width: 100%;
    height: calc(100% - 10px);
    margin-bottom: 20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: greenyellow;
}

.double-card-lower {
    width: 100%;
    height: calc(100% - 10px);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: khaki;
}

.triple-card {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}

.triple-card-left {
    width: calc(45% - 10px);
    height: 100%;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
}

.triple-card-left-upper, .triple-card-left-lower {
    width: 100%;
    height: calc(50% - 10px);
    border-radius: 10px;
    background-color: aquamarine;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.triple-card-left-lower {
    margin-top: 10px;
}

.triple-card-left-upper {
    margin-bottom: 10px;
}

.triple-card-right {
    width: calc(55% - 10px);
    margin-left: 0px;
    height: 100%;
    border-radius: 10px;
    background-color: orange;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.single-card img,  .double-card img, .triple-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

