	padding-left: 100px;
	overflow: hidden;
}

.team-header {
	display: flex;
	flex-direction: row;
	/* background-color: #ABABAB1a; */
	align-items: center;
	justify-content: space-between;
	padding: 20px 10px 0 10px;
	border-radius: 8px;
	transition: 0.25s ease-in-out;
}

.back-text {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin: 20px 10px;
	padding-top: 10px;
	cursor: pointer;
}

.back-text * {
	margin-right: 10px;
}



.team-header-left {
	text-align: left;
	max-width: 100%;
	transition: 0.25s;
}

.team-header-right {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.team-head {
	padding-left: 0px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.team-head-data {
	text-align: right; 
	padding-left: 0px;
}

.team-head-data * {
	margin: 7px 0;
}

.team-head-social {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-end;
}

.team-head-social * {
	margin: 5px 0px;
}

.team-head img {
	width: 120px;
	height: 120px;
	border-radius: 50%;
}

.team-head * {
	margin-left: 20px;
}

.member-box {
	background-color: transparent;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	justify-content: flex-start;
	padding: 0 15px;
	transition: 0.2s ease-in-out;
	cursor: pointer;
}

.member-box:hover {
	transform: scale(1.05);
	transition: 0.2s ease;
}

.member-box * {
	margin-bottom: 10px;
}

.member-box-photo {
	width: 120px;
	height: 120px;
	margin-top: 15px;
	background-color: #ABABAB1a;
	border-radius: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	transition: 0.23s ease;
	position: relative;
}

.member-box-photo:hover img{
	-webkit-filter: brightness(0.4);
	        filter: brightness(0.4);
	transition: 0.23s ease;
}

.member-box-photo img {
	object-fit: cover;
	width: 100%;
	height: 100%;
	transition: 0.25s ease;
}

.member-container {
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	grid-row-gap: 20px;
	row-gap: 20px;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;
	margin-top: 20px;
}

.team-member-social {
	display: none;
	transition: 0.25s ease;
}

.member-box-photo:hover .team-member-social {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-around;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 9999;
	transition: 0.25s ease;
}

.team-member-social a {
	width: auto;
	height: 20px;
}
.change-align {
	text-align: center;
}
