@media only screen and (max-width: 600px) {
	.team-full-container {
		width: 100%;
		display: flex;
		flex-direction: column;
		background-color: var(--background);
		color:white;
		margin-top: 0;
		padding-left: 0px;
		overflow: hidden;
		height: auto;
		padding: 0 15px;
	}
	
	.team-header {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: unset;
		padding: 20px 10px 0 10px;
		border-radius: 8px;
		transition: 0.25s ease-in-out;
	}
	.team-header-left {
		text-align: center;
		max-width: 100%;
		transition: 0.25s;
	}

	.team-header-left * {
		margin-bottom: 15px;
	}
	
	.team-header-right {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.team-head {
		padding-left: 0px;
		display: flex;
		margin-top: 30px;
		flex-direction: column-reverse;
		align-items: center;
		justify-content: unset;
	}

	.team-head * {
		margin-left: 0;
	}
	.team-head-data {
		text-align: center; 
		padding-left: 0px;
	}
	
	.team-head-data * {
		margin: 7px 0;
	}
	
	.team-head-social {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-around;
	}
	
	.team-head-social * {
		margin: 5px 0px;
	}

	.member-container {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		grid-row-gap: 20px;
		row-gap: 20px;
		grid-column-gap: 0px;
		-webkit-column-gap: 0px;
		        column-gap: 0px;
		margin-top: 10px;
	}
	.member-box {
		background-color: transparent;
		display: flex;
		flex-direction: row;
		align-items: center;
		text-align: center;
		justify-content: flex-start;
		padding: 0 15px;
		transition: 0.2s ease-in-out;
		cursor: pointer;
		margin-bottom: 8px;
	}
	.member-box-photo {
		width: 50px;
		height: 50px;
		background-color: #ABABAB1a;
		border-radius: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		overflow: hidden;
		transition: 0.23s ease;
		position: relative;
		margin-right: 20px;
		margin-top: 0;
	}
	.member-box * {
		margin-bottom: 0;
	}
	.change-align {
		text-align: left;
	}
	.no-display {
		display: none;
	}
	
}
/* CSS for sidebar */

.sidebar-container {
    width: 100px;
    height: 100vh;
    z-index: 9999;
    top: 0;
    left: 0;
    position: fixed;
    background: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.app-logo-container {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
}

.menu-container {
    width: 100%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 50% 0 80% 0;
}

.menu-option {
    width: 60px;
    height: 60px;
    background-color: transparent;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 0.25s ease-in-out;
    color: var(--lightgrey);
}

.active-menu {
    color: var(--blue);
}

.menu-option:hover:not(.active-menu) {
    color: var(--darkgrey);
    transition: 0.25s ease-in-out;
}

.menu-icon {
    width: 20px;
    color: inherit;
    height: 20px;
}

.react-toggle {
    transform: rotate(-90deg);
}
.react-toggle-thumb {
    width: 19px;
    height: 19px;
    top: 2.5px;
    left: 2.5px;
    border: none;
}

.toggle.react-toggle--focus .react-toggle-thumb {
	box-shadow: none;
}
.chartToggle.react-toggle--focus .react-toggle-thumb {
	box-shadow: none;
}

.toggle * {
	box-shadow: none;
}

.toggle.react-toggle--checked .react-toggle-track {
	background-color: #d3d3d3;
}
.chartToggle.react-toggle--checked .react-toggle-track {
	background-color: var(--blue);
}
.toggle.react-toggle--checked .react-toggle-track:hover{
	background-color: #d3d3d3;
}

.toggle.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
	background-color: #d3d3d3;
}

.toggle.react--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
	background-color: #d3d3d3;
}

.toggle.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
	box-shadow: none;
}

.chartToggle.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
	box-shadow: none;
}

.toggle.react-toggle--checked .react-toggle-thumb {
	border: none;
}

.go-up {
    width: 40px;
    height: 40px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 30px;
    z-index: 99999;
    left: calc(100vw - 70px);
    border-radius: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #ABABAB1a;
    cursor: pointer;
}

@media only screen and (max-width: 600px)  {
    .sidebar-container {
        width: 100%;
        height: 60px;
        bottom: 0;
        left: 0;
        top: unset;
        background: var(--background);
        flex-direction: row;
        justify-content: space-around;
    }

    .app-logo-container {
        width: 15%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: transparent;
    }
    
    .menu-container {
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: unset;
    }
    .go-up {
        width: 40px;
        height: 40px;
        position: -webkit-sticky;
        position: sticky;
        bottom: 80px;
        z-index: 99999;
        left: calc(100vw - 20px);
        border-radius: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: #ABABAB1a;
        cursor: pointer;
    }
    .team-sidebar {
		display: none;
	}
}
